import { Component } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrl: './login.component.scss'
})
export class LoginComponent {
  email: string = '';
  password: string = '';
  loginError: string = ''; // Variabile per memorizzare il messaggio di errore

  constructor(private afAuth: AngularFireAuth, private router: Router) {}

  async login() {
    try {
      await this.afAuth.signInWithEmailAndPassword(this.email, this.password);
      this.router.navigate(["user/certificates"]);
    } catch (error) {
      console.error('Error logging in:', error);
      // Handle Firebase authentication errors
      if (error.code === 'auth/user-not-found') {
        this.loginError = 'User not found. Check the entered email.';
      } else if (error.code === 'auth/wrong-password') {
        this.loginError = 'Incorrect password. Please try again.';
      } else {
        this.loginError = 'Error logging in. Please try again later.';
      }
    }
  }
}
